.search-wrapper {
  width: 40%;
  display: flex;
  align-items: center;
  color: $white-4; }

.search-wrapper div {
  cursor: pointer; }

.search-wrapper div:hover svg, .search-wrapper div:hover {
  fill: $white-2;
  stroke: $white-2;
  color: $white-2; }

.search-wrapper__filters-dropdown {
  box-sizing: border-box;
  width: 13%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  font-weight: bold;
  padding-left: 15px; }

.search-wrapper__filters-dropdown div {
  flex: 1; }

.search-wrapper__filters-dropdown__actual-filter {
  text-align: end; }

.search-wrapper__filters-dropdown__arrow-icon {
  text-align: start;
  padding-left: 5px; }

.search-wrapper__filters-dropdown__arrow-icon svg {
  position: relative;
  top: 3px;
  transform: rotate(180deg);
  stroke-width: 50px; }

.search-input {
  width: 90%;
  border: none;
  font-size: 1.1em;
  color: $white-4;
  outline: none;
  border-left: 1px solid $white-4;
  padding-left: 10px; }

.search-input::placeholder {
  color: $white-5; }

.search-input, .search-wrapper__filters-dropdown, .search-icon-wrapper {
  height: 50px;
  background: $black-3; }

.search-icon-wrapper {
  display: flex;
  align-items: center;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  padding: 0 20px; }

